body {
    font-family: 'Rubik', sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

header {
    height: 70px;
}

header nav  {
    height: 50px;
}

header nav > div {
    float: left;
}

header nav > div > a {
    margin-right: 20px;
    line-height: 70px;
}

header nav > div > i {
    font-size: 32px;
    margin-right: 30px;
    line-height: 70px;
}

#logo-menu {
    font-size: 32px;
    line-height: 70px;
}

.logo {
    margin-bottom: 16px;
    margin-left: 20px;
}

div#search-form {
    height: 40px;
    margin-top: 15px;
    font-size: 14px;
}

div#search-form input[type="text"] {
    padding: 10px;
    display: block;
}

div.search-text {
    border: 1px solid #e3e3e3;
    padding: 10px 30px;
    border-top-left-radius: 20px;
    border-bottom-left-radius: 20px;
    width: fit-content;
}

div.search-icon {
    border: 1px solid #e3e3e3;
    padding: 10px 20px;
    border-top-right-radius: 20px;
    border-bottom-right-radius: 20px;
    width: fit-content;
    border-left: none !important;
}

div.search-input {
    border: 1px solid #e3e3e3;
}

div.search-input input {
    border: none;
}
.left {
    float: left;
}

section#slider {
    margin-top: 0 !important;
}

.slide {
    padding: 0;
    overflow: hidden;
}

.slide::before, .slide:hover::before  {
    content: '';
    width: inherit;
    position: absolute;
    display: block;
    height: 100%;
    bottom: 0; 
    background: rgb(0,0,0);
    background: linear-gradient(0deg, rgba(0,0,0,0.7329306722689075) 0%, rgba(0,0,0,0.31836484593837533) 50%, rgba(255,251,251,0) 100%);
    z-index: 3;
}

.slide img {
    max-height: 480px;
    transition: all .3s;
}

.slide:hover {
    cursor: url('/images/slide2.png') 2 2, pointer;
}

.slide:hover img {
    transform: scale(1.2);
    transition: all .3s;
}

.img-full {
    width: 100%;
}

.movie-info {
    position: absolute;
    bottom: 0;
    color: #fff;
    padding: 15px;
    z-index: 9;
}

span.imdb {
    font-size: 24px;
    display: flow-root;
    line-height: 30px;
    margin-bottom: 20px;
}

.movie-poster {
    overflow: hidden;
}

.movies .movie-poster {
    margin-bottom: 20px;    
}

.movie-poster::before {
    content: url(/images/play3.png);
    position: absolute;
    width: 120px;
    height: 120px;
    color: #fff;
    top: calc(50% - 60px);
    left: calc(50% - 60px);
    z-index: 99;
    display: block;
    opacity: 0;
    transition: all .3s;
}

.movie-info svg {
    margin-right: 10px;
}

.slide:hover .movie-poster::before {
    content: url(/images/play3.png);
    position: absolute;
    width: 120px;
    height: 120px;
    color: #fff;
    top: calc(50% - 60px);
    left: calc(50% - 60px);
    z-index: 99;
    display: block;
    cursor: pointer;
    opacity: 1;
    transition: all .3s;
}

.tab-pane div:hover > .movie-poster::before {
    content: url(/images/play3.png);
    position: absolute;
    width: 120px;
    height: 120px;
    color: #fff;
    top: calc(40% - 60px);
    left: calc(50% - 60px);
    z-index: 99;
    display: block;
    cursor: pointer;
    opacity: 1;
    transition: all .3s;
}

#spotlight h3 {
    font-weight: 400;
    border-bottom: 1px solid #e5e5e5;
    padding-bottom: 20px;
}

#spotlight svg {
    margin-right: 10px;
}

#spotlight nav {
    margin-top: 30px;
    margin-bottom: 30px;
}

#spotlight nav a {
    margin-right: 30px;
    color: #000;
}

#spotlight nav a.selected {
    border-bottom: 2px solid #dd3333;
    color: #dd3333;
    padding-bottom: 5px;
}

#spotlight nav a:hover {
    color: #dd3333;   
    text-decoration: none;
}

#spotlight h4 {
    font-weight: 400;
}

section {
    margin-top: 60px;
    display: flow-root;
}

.tab-pane {
    margin-left: -15px;
    margin-right: -15px;
}

.mbs {
    margin-bottom: 10px !important;
}

.mtm {
    margin-top: 20px !important;
}

.mbm {
    margin-bottom: 20px !important;
}

.movies {
    display: flow-root;
    position: relative;
}

.movies > div:hover img {
    transform: scale(1.2);
}

#trailer {
    background-image: url("/images/trailer-bg.jpg");
    background-position: center center;
    background-size: cover;
    background-repeat: no-repeat;
    padding: 60px 0 0 20px;
}

#trailer h3 {
    color: #fff;
    border-bottom: 1px solid #e5e5e5;
    padding-bottom: 20px;
}

#trailer h3::before {
    content: '\E800';
    font-family: 'fontello';
    display: block;
    float: left;
    margin-right: 20px;
    color: #dd3333; 
}

#trailer .big-trailer {
    margin-top: 30px;
    position: relative;
}

#trailer .trailer-info {
    position: absolute;
    background: rgba(0, 0, 0, 0.5);
    color: #fff;
    bottom: 5px;
    padding: 20px;
    width: 100%;
}

#trailer .small-trailer .trailer-info {
    padding: 10px;
    width: calc(100% - 30px);
}

.align-right {
    text-align: right;
}

#social {
    font-size: 72px;
    margin: 20px 0 !important;
}

#social i {
    cursor: pointer;
}

#social i.icon-apple:hover {
    background: -webkit-linear-gradient(#eee, #333);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
}

#social i.icon-android:hover {
    color: #a4c639;
}

footer {
    background: url('/images/footer-bg.jpg');
    padding: 30px 0;
}

footer nav a {
    color: #fff;
    margin-right: 30px;
    font-size: 14px;
    font-weight: 300;
}

footer nav a:hover {
    color: #fff;
    text-decoration: none;
}

a.right {
    float: right;
}

.nav-tabs .nav-link, .nav-tabs .nav-item.show .nav-link:not(.active), .nav-tabs {
    border: none !important;
}

.nav-link:not(.active) {
    padding: 0 !important;
    border: none !important;
}

.nav-link.active {
    border-bottom: 2px solid #dd3333 !important;
    color: #dd3333 !important;
    padding-bottom: 5px !important;
    border-bottom: 2px solid #dd3333 !important;
    padding: 0 !important;
}

.search-input {
    padding: 0;
}

.search-input input[type='text'] {
    border: none;
    width: 100%;
}

#search-result {
    width: 100%;
    height: 150px;
    background: #fff;
    position: absolute;
    z-index: 9;
    overflow-y: scroll;
}

.search-movie {
    display: flow-root;
    padding: 10px 0;
    border-bottom: 1px solid #e3e3e3;
}

.search-movie .movie-poster {
    float: left;
}

.search-movie .movie-poster img {
    width: inherit;
}

.search-movie span.imdb {
    font-size: 14px;
    line-height: 14px;
    margin-bottom: 0;
}

.search-movie span.imdb svg {
    margin-right: 5px;
}

.tab-pane img.arrow {
    position: absolute;
    top: calc(50% - 30px);
    cursor: pointer;
}

.tab-pane img.arrow-left {
    left: -60px;
}

.tab-pane img.arrow-right {
    right: -60px;
}

.mtl {
    margin-top: 60px;
}

#player {
    position: fixed;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    background: rgba(0, 0, 0, 0.9);
    z-index: 99;
}

#player > iframe {
    width: 90%;
    margin-top: 5%;
    height: 80%;
    margin-left: 5%;
}

#player svg {
    margin-right: 10px;
    font-size: 40px;
    position: absolute;
    right: calc(5% - 20px);
    top: 5%;
    color: #fff;
    cursor: pointer;
}